import React, { useEffect, useState, useContext } from 'react'
import { Form, Input, Select } from 'antd'
import { createLeague } from '../services/geekLeague'
import { getUsers } from '../services/user'
import { getProfile } from '../services/auth'
import { Context } from '../context'
import { Loader } from '../components'
import { openNotification } from '../helpers'
import '../styles/newGeekleague.css'

const { Option } = Select

const NewGeekLeague = ({ history, loading }) => {
    const { user, loginUser } = useContext(Context)
    const [form] = Form.useForm()
    const [users, setUsers] = useState(null)
    const [creating, setCreating] = useState(false)

    useEffect(() => {
        const fetchUsers = async () => {
            const allUsers = await getUsers()
            const users = allUsers.filter(oneUser => oneUser._id !== user?._id)
            setUsers(users)
        }
        fetchUsers()
    }, [user])

    const newLeague = async (values) => {
        if (!values.name || !values.geeks || values.geeks.length < 1) return openNotification('warning', 'Attention', 'Tous les champs sont requis.')
        setCreating(true)
        const geekLeague = await createLeague(values)
        openNotification('success', `Ligue "${values.name}" cre`)
        history.push(`/myGeekLeagues/${geekLeague._id}`)
        setCreating(false)
        const user = await getProfile()
        loginUser(user)
    }

    return <div className='geekleague-bg'>
        {!users || creating || loading ? (

            <Loader />

        ) : (

                <div className='geekleague-form'>

                    <div className='col-10 offset-1 col-sm-8 offset-sm-2 col-xl-6 offset-xl-3'>

                        <h2>Crer une ligue</h2>

                        <Form
                            form={form}
                            layout='vertical'
                            name="basic"
                            initialValues={{
                                remember: true,
                            }}
                            onFinish={newLeague}
                        >

                            <Form.Item
                                type='text'
                                label="Nom de la ligue :"
                                name="name"
                                rules={[
                                    {
                                        required: true,
                                        message: `Le nom de ligue est ncessaire.`,
                                    },
                                ]}
                            >

                                <Input
                                    style={{ borderRadius: 15.8 }}
                                    placeholder='Ma Ligue Geek'
                                />

                            </Form.Item>

                            <Form.Item
                                type='text'
                                label="Slectionne d'autres geeks :"
                                name="geeks"
                                rules={[
                                    {
                                        required: true,
                                        message: `Tu ne peux pas crer de ligue sans autres geeks.`,
                                    },
                                ]}
                            >

                                <Select
                                    mode="multiple"
                                    style={{ width: '100%', borderRadius: 15.8, overflow: 'hidden', textAlign: 'left' }}
                                    placeholder="Ajoute des geeks  ta ligue !"
                                    optionLabelProp="label"
                                    optionFilterProp='label'
                                >

                                    {users?.map(user => <Option
                                        key={user._id}
                                        value={user._id}
                                        label={user.username}
                                    >

                                        <div className="demo-option-label-item">
                                            <span role="img" aria-label={user.username}>
                                                <img
                                                    src={user.photo}
                                                    alt="profile"
                                                    className='profile-pic-preview'
                                                />
                                            </span>
                                            &nbsp;&nbsp;{user.username}
                                        </div>

                                    </Option>)}

                                </Select>

                            </Form.Item>

                            <div style={{ display: 'flex', justifyContent: 'center' }}>
                                <button
                                    type='submit'
                                    className='btn my-btn create-league-btn'
                                    style={{ marginTop: 10 }}
                                >
                                    Crer ligue
                                </button>
                            </div>

                        </Form>

                    </div>

                </div>
            )
        }
    </div>
}

export default NewGeekLeague